.faucet-form-wrapper {
    margin-top: 2rem;
    width: 25%;
}
#faucet-claim-button {
    margin-top: 2rem;
}

.claim-amount-description {
    font-size: 0.9rem;
    margin-top: 1rem;
    color: #999999;
}

.error-message {
    color: #FD4040;
    font-size: 0.9rem;
}

@media( max-width: 640px ){
    .faucet-form-wrapper {
        width: 100%;
    }
}

@media( min-width: 640px) and ( max-width: 768px ){
    .faucet-form-wrapper {
        width: 80%;
    }
}

@media( min-width: 768px) and ( max-width: 992px ) {
    .faucet-form-wrapper {
        width: 60%;
    }
}